import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';


import { IndexComponent } from './index/index.component';
import {LoginComponent} from './login/login.component';
import {GraduateProjectComponent} from './graduate-project/graduate-project.component';
import {AddGraduateProjectComponent} from './graduate-project/add-graduate-project.component';
import {UpdateGraduateProjectComponent} from './graduate-project/update-graduate-project.component';
import {LogoutComponent} from './login/logout.component';

const routes: Routes = [
  {path: '', component: LoginComponent, pathMatch: 'full'},
  {path: 'logout', component: LogoutComponent},
  {path: 'index', component: IndexComponent, children: [
      {path: 'graduateProject', component: GraduateProjectComponent},
      {path: 'addGp', component: AddGraduateProjectComponent},
      {path: 'updateGp/:id', component: UpdateGraduateProjectComponent},
      ]},
];
@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
