import { Component, OnInit } from '@angular/core';
import {GraduateProject1, GraduateProjectCategory, GraduateProjectType, Teacher} from '../entity';
import {UserService} from '../user.service';
import {UniversityService} from '../university.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-add-graduate-project',
  templateUrl: './add-graduate-project.component.html',
  styleUrls: ['./add-graduate-project.component.css']
})
export class AddGraduateProjectComponent implements OnInit {
  graduateProjectCategorys: GraduateProjectCategory[];
  graduateProjectTypes: GraduateProjectType[];
  graduateProjectCategory: GraduateProjectCategory;
  graduateProjectType: GraduateProjectType;
  graduateProject: GraduateProject1;
  teacher: Teacher;

  constructor(private bysjService: UniversityService,
              private userService: UserService,
              private router: Router) {}

  ngOnInit() {
    this.getGraduateProjectCategorys();
    this.getProjectTypes();
    this.getTeacher();
  }
  getGraduateProjectCategorys() {
    this.bysjService.getProjectCategorys().subscribe( res => this.graduateProjectCategorys = res);
  }
  getGraduateProjectCategory(id: number) {
    this.bysjService.getProjectCategory(id).subscribe( res => this.graduateProjectCategory = res);
  }
  getProjectTypes() {
    this.bysjService.getProjectTypes().subscribe(res => this.graduateProjectTypes = res);
  }
  getProjectType(id: number) {
    this.bysjService.getProjectType(id).subscribe( res => this.graduateProjectType = res);
  }
  getTeacher() {
    this.teacher = this.userService.getTeacher();
  }
  goBack() {
    this.router.navigateByUrl('index/graduateProject');
  }
  save(title: string, gpc: GraduateProjectCategory, gpt: GraduateProjectType): void {
    this.graduateProject = {title: title.trim(),
      graduateProjectCategory: this.graduateProjectCategory, graduateProjectType: this.graduateProjectType,
      teacher: {id: this.teacher.id,
        name: this.teacher.name,
        no: this.teacher.no
      }};
    if (!title) { return; }
    this.bysjService.addGraduateProject(this.graduateProject).subscribe(
      (res) => {
        alert(res.message);
        this.goBack();
      }
    );
  }

}
