import {Component, Input, OnInit} from '@angular/core';
import {GraduateProject, GraduateProjectCategory, GraduateProjectType} from '../entity';
import {ActivatedRoute} from '@angular/router';
import {UniversityService} from '../university.service';
import { Location } from '@angular/common';


@Component({
  selector: 'app-update-graduate-project',
  templateUrl: './update-graduate-project.component.html',
  styleUrls: ['./update-graduate-project.component.css']
})
export class UpdateGraduateProjectComponent implements OnInit {
  graduateProject: GraduateProject;
  graduateProjectCategorys: GraduateProjectCategory[];
  graduateProjectTypes: GraduateProjectType[];
  constructor(private route: ActivatedRoute, private bysjService: UniversityService,  private location: Location) { }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.bysjService.getGraduateProject(id).subscribe( res =>  this.graduateProject = res );
    this.bysjService.getProjectCategorys().subscribe( res => this.graduateProjectCategorys = res);
    this.bysjService.getProjectTypes().subscribe( res => this.graduateProjectTypes = res);
  }
  goBack(): void {
    this.location.back();
  }
  save() {
    this.bysjService.updateGraduateProject(this.graduateProject).subscribe(
      (res) => {
        alert(res.message);
        this.goBack();
      }
    );
  }
  compareFn(o1: GraduateProject, o2: GraduateProject): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
}
