import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import {
  Response,
  GraduateProjectType, GraduateProjectCategory, GraduateProject, GraduateProject1
} from './entity';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
@Injectable({
  providedIn: 'root'
})
export class UniversityService {
  response: Response;
  private userUrl = '../login.ctl';
  private graduateProjectURL = '../graduateProject.ctl';
  private projectCategoryURL =  '../graduateProjectCategory.ctl';
  private projectTypeURL = '../graduateProjectType.ctl';
  private logoutUrl = '../logout.ctl';

  constructor(private http: HttpClient) { }

  // 验证登录用户账号密码是否正确
  checkUser(user: { username: string, password: string }): Observable<any> {
    return this.http.post<any>(this.userUrl, user);
  }
  logOut(): Observable<any> {
    return this.http.get<any>(this.logoutUrl);
  }
  /*获取老师对应课题*/
  getGraduateProjects(teacherId: number): Observable<GraduateProject[]> {
    const url = this.graduateProjectURL + '?teacherId=' + teacherId;
    return this.http.get<GraduateProject[]>(url).pipe();
  }
  getGraduateProject(id: number): Observable<GraduateProject> {
    const url = this.graduateProjectURL + '?id=' + id;
    return this.http.get<GraduateProject>(url).pipe();
  }
  /*获取课题类型*/
  getProjectCategorys(): Observable<GraduateProjectCategory[]> {
    return this.http.get<GraduateProjectCategory[]>(this.projectCategoryURL).pipe();
  }
  getProjectCategory(id: number): Observable<GraduateProjectCategory> {
    const url = this.projectCategoryURL + '?id=' + id;
    return this.http.get<GraduateProjectCategory>(url).pipe();
  }
  /*获取课题种类*/
  getProjectTypes(): Observable<GraduateProjectType[]> {
    return this.http.get<GraduateProjectType[]>(this.projectTypeURL).pipe();
  }
  getProjectType(id: number): Observable<GraduateProjectType> {
    const  url = this.projectTypeURL + '?id' + id;
    return this.http.get<GraduateProjectType>(url).pipe();
  }
  delete(graduateProject: GraduateProject | number): Observable<Response> {
    const id = typeof graduateProject === 'number' ? graduateProject : graduateProject.id;
    return this.http.delete<Response>(this.graduateProjectURL + '?id=' + id, httpOptions).pipe();
  }
  addGraduateProject(gp: GraduateProject1): Observable<Response> {
    return this.http.post<Response>(this.graduateProjectURL, gp, httpOptions).pipe();
  }
  updateGraduateProject(gp: GraduateProject): Observable<Response> {
    return this.http.put<Response>(this.graduateProjectURL, gp, httpOptions).pipe();
  }
  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}

