import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import { UniversityService } from '../university.service';
import {UserService} from '../user.service';
import {Teacher, User} from '../entity';

@Component({
  selector: 'app-login',
  templateUrl: './Login.component.html',
  styleUrls: ['./Login.component.css']
})
export class LoginComponent implements OnInit {
  private user: User;
  private uname: string;
  private password: string;
  userName: string;
  teacher: Teacher;

  constructor(private router: Router, private loginService: UniversityService, private userService: UserService) { }

  ngOnInit() { // 组件被创建
  }
  login(un: string, up: string) { // 登录跳转页面
    this.uname = un;
    this.password = up;
    this.loginService.checkUser({username: this.uname, password: this.password})
      .subscribe((res) => {
        if (res.name) {
          this.teacher = {id: res.id, name: res.name, no: res.no}
          this.userService.setTeacher(this.teacher);
          this.router.navigateByUrl('index/graduateProject');
        } else {
          alert(res.message);
        }
      });
  }
}
