import { Component, OnInit } from '@angular/core';
import {GraduateProject} from '../entity';
import {UniversityService} from '../university.service';
import {UserService} from '../user.service';

@Component({
  selector: 'app-graduate-project',
  templateUrl: './graduate-project.component.html',
  styleUrls: ['./graduate-project.component.css']
})
export class GraduateProjectComponent implements OnInit {
  private graduateProjects: GraduateProject[];
  constructor(private graduateProjectService: UniversityService, private userService: UserService) { }

  ngOnInit() {
    this.getGraduateProjects();
  }
  getGraduateProjects(): void {
    this.graduateProjectService.getGraduateProjects(this.userService.getTeacher().id).subscribe(
      res => { this.graduateProjects = res; }
    );
  }
  delete(graduateProject: GraduateProject | number): void {
    this.graduateProjects = this.graduateProjects.filter(h => h !== graduateProject);
    this.graduateProjectService.delete(graduateProject).subscribe( res => alert(res.message));
  }
}
